import { Actions } from '@app/actions/shared';
import { browser_api } from '@app/browser_api';
import { Events, type ShowPageLoaderEvent } from '@app/events';
import { Feature } from '@app/features/types/Feature';
import {
  ChangeServerToastClickedMessage,
  isMessageOfType,
} from '@app/messages/shared';
import { LocalStorageKey } from '@app/storage/local/types/sharedTypes';
import { waitForBridgeStorageLoaded } from '@bridgeStore/waitForBridgeStorageLoaded';
import {
  showToast,
  hideAllToasts,
  hideAllToastsByType,
} from '@content/components/customToast';
import i18n from '@content/i18n';
import { getFeatureStoreContentScript } from '@content/utils/getFeatureStoreContentScript';
import {
  hideConnectionFailed,
  showConnectionFailed,
} from '../components/ConnectionFailed';
import {
  getLocalStorageData,
  setLocalStorageData,
} from '@app/storage/local/shared';
import {
  DEFAULT_CHANGE_SERVER_TIMEOUT_MS,
  TWELVE_HOURS_MS,
} from '@utils/proxy/constants';

browser_api.runtime.onMessage.addListener((message: unknown) => {
  try {
    if (isMessageOfType(message, Actions.NetworkConnectionRetry)) {
      void showChangeServerToast();
    }
  } catch (error) {
    console.error('error', error);
  }
});

browser_api.runtime.onMessage.addListener(async (message: unknown) => {
  try {
    if (isMessageOfType(message, Actions.NetworkConnectionRetrySuccess)) {
      hideConnectionFailed();
      void hideAllToastsByType('changeServer');

      const { featuresList } = await getFeatureStoreContentScript();
      const reloadTabsOnNetworkError =
        featuresList[Feature.ReloadTabsOnNetworkError];

      if (reloadTabsOnNetworkError) {
        window.location.reload();
      } else {
        void hideAllToastsByType('changeServer');
      }
    }
  } catch (error) {
    console.error('error', error);
  }
});

browser_api.runtime.onMessage.addListener((message: unknown) => {
  try {
    if (isMessageOfType(message, Actions.NetworkConnectionRetryFail)) {
      showConnectionFailed();
      document.dispatchEvent(
        new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
          detail: { isShown: false },
        }),
      );
      showChangeServerToast();
    }
  } catch (error) {
    console.error('error', error);
  }
});

browser_api.runtime.onMessage.addListener((message: unknown) => {
  if (isMessageOfType(message, Actions.HideAllToasts)) {
    hideAllToasts();
  }
});

browser_api.runtime.onMessage.addListener((message: unknown) => {
  if (isMessageOfType(message, Actions.HideAllToastsByType)) {
    hideAllToastsByType(message.payload.type);
  }
});

document.addEventListener('DOMContentLoaded', async () => {
  try {
    const store = await waitForBridgeStorageLoaded();
    const isNetworkError = store[LocalStorageKey.IsNetworkError];
    const isNetworkErrorHandlingInProgress =
      store[LocalStorageKey.isNetworkErrorHandlingInProgress];

    if (isNetworkError && isNetworkErrorHandlingInProgress) {
      document.dispatchEvent(
        new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
          detail: {
            isShown: true,
          },
        }),
      );
    } else if (isNetworkError) {
      showConnectionFailed();
    }
  } catch (error) {
    console.error('error', error);
  }
});
async function showChangeServerToast() {
  const {
    changeServerToastUpdatedAt,
    lastManualServerChangeAtAllInternet,
    lastManualServerChangeAtYoutube,
    status,
  } = await getLocalStorageData([
    LocalStorageKey.LastManualServerChangeAtAllInternet,
    LocalStorageKey.LastManualServerChangeAtYoutube,
    LocalStorageKey.ChangeServerToastUpdatedAt,
    LocalStorageKey.Status,
  ]);

  let toastDescription = i18n.t(
    'content:toast.changeServer.textChangeServerAvailable',
  );

  const currentTime = new Date().getTime();

  if (changeServerToastUpdatedAt) {
    const changeServerToastUpdateAtTime = new Date(
      changeServerToastUpdatedAt,
    ).getTime();

    if (
      currentTime - changeServerToastUpdateAtTime <
      DEFAULT_CHANGE_SERVER_TIMEOUT_MS
    ) {
      return;
    }
  }

  const tooEarlyToChangeAllInternetServer =
    status === 'Paid' || status === 'Trial'
      ? lastManualServerChangeAtAllInternet &&
        currentTime - lastManualServerChangeAtAllInternet < TWELVE_HOURS_MS
      : true;

  const tooEarlyToChangeYoutubeServer =
    lastManualServerChangeAtYoutube &&
    currentTime - lastManualServerChangeAtYoutube < TWELVE_HOURS_MS;

  if (tooEarlyToChangeAllInternetServer && tooEarlyToChangeYoutubeServer) {
    toastDescription = i18n.t('content:toast.changeServer.text');
  }

  showToast({
    title: i18n.t('content:toast.changeServer.title'),
    text: toastDescription,
    buttons:
      tooEarlyToChangeAllInternetServer && tooEarlyToChangeYoutubeServer
        ? []
        : [
            {
              text: i18n.t('content:toast.changeServer.button'),
              onClick: async () => {
                await handleChangeServerToastClick();
              },
              accent: true,
            },
          ],
    type: 'changeServer',
    closeBehavior: 'allInAllTabsByType',
    sideEffectOnClose: async () => {
      await setLocalStorageData({
        [LocalStorageKey.ChangeServerToastUpdatedAt]: new Date().toString(),
      });
    },
  });
}

async function handleChangeServerToastClick(): Promise<void> {
  try {
    await browser_api.runtime.sendMessage<ChangeServerToastClickedMessage>({
      action: Actions.ChangeServerToastClicked,
    });
  } catch (error) {
    console.error('Error handling change server toast click:', error);
  }
}
